/* 引入全局样式 */
/* 统一样式*/
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', '微软雅黑', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #333;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  font-weight: 400;
  width: 100%;
  -webkit-font-smoothing: antialiased;
}
/**************** 工具类样式 *****************/
/* 水平flex布局 */
.hfq-cell {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
}
.hfq-cell-primary {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
}
.hfq-cell-ft {
  text-align: right;
}
.bg-0 {
  background: #fdc162 url('assets/css/~src/assets/img/bg_0.jpeg') no-repeat center center;
}
.bg-1 {
  background: #fd6a62 url('assets/css/~src/assets/img/bg_1.jpeg') no-repeat center center;
}
.bg-2 {
  background: #f68dbb url('assets/css/~src/assets/img/bg_2.jpeg') no-repeat center center;
}
.bg-3 {
  background: #0ac2d2 url('assets/css/~src/assets/img/bg_3.jpeg') no-repeat center center;
}
.bg-4 {
  background: #7bb7fa url('assets/css/~src/assets/img/bg_4.jpeg') no-repeat center center;
}
.bg-5 {
  background: #60d7a9 url('assets/css/~src/assets/img/bg_5.jpeg') no-repeat center center;
}
html,
body,
.app {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.app {
  background-color: #000;
  -webkit-perspective: 1200px;
          perspective: 1200px;
}
.app .app-router-view {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  visibility: visible;
}
.app > .m-tabs {
  position: fixed;
  width: 900px;
  height: 100px;
  z-index: 1000;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.app > .m-tabs .el-tab-pane {
  font-size: 0;
}
.app > .m-tabs .el-tabs__item {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.app > .m-tabs .el-tag {
  margin-bottom: 8px;
  margin-right: 8px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.app > .m-tabs .el-tag + .el-tag {
  margin-left: 0;
}
.app > .m-tabs .el-tag:hover {
  background-color: rgba(64, 158, 255, 0.1);
  color: #409eff;
  border: 1px solid rgba(64, 158, 255, 0.2);
}
.github {
  position: fixed;
  z-index: 1000;
  top: 20px;
  left: 20px;
  opacity: 0.8;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.github:hover {
  opacity: 1;
}



